/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
html {
  font-size: 16px; }

body {
  font-family: "Roboto", sans-serif;
  font-size: 1rem;
  margin: 0; }

a {
  line-height: normal;
  padding: 0;
  border-radius: 0;
  border: none;
  background: none;
  color: #004677;
  vertical-align: baseline;
  text-align: start;
  text-decoration: none; }
  a:hover, a.hover {
    color: #005a99;
    background: none; }
  a:focus, a.focus {
    color: #005a99;
    background: none; }
  a [disabled] {
    color: #a5a5a5;
    background: none; }
  a:active, a.active {
    color: #3a7da9;
    background: none; }
  a.disabled, a:disabled, a.disabled:hover, a:disabled:hover {
    color: #a5a5a5;
    background: none; }

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700; }

small {
  font-size: 0.75rem;
  font-weight: normal; }

.lead {
  font-size: 1.313rem; }

h1 {
  font-size: 2.25rem; }
  h1.small {
    font-size: 1.438rem; }

h2 {
  font-size: 1.875rem; }
  h2.small {
    font-size: 1.25rem; }

h3 {
  font-size: 1.5rem; }
  h3.small {
    font-size: 1rem; }

h4 {
  font-size: 1.125rem; }
  h4.small {
    font-size: 0.875rem; }

h5 {
  font-size: 0.875rem; }
  h5.small {
    font-size: 0.688rem; }

h6 {
  font-size: 0.75rem; }
  h6.small {
    font-size: 0.563rem; }
